import React from 'react';
import styles from './youtube.module.css';
//import { Item } from 'semantic-ui-react';
import {Card,CardActionArea,CardMedia,CardContent,Typography,Divider} from '@material-ui/core';
const VideoList=(props)=>{
    console.log(props.data);
    function handleClick(index){       
       props.onSelectItem(index,props.from);
    }
    return( <div className={styles.cardContainer}>
        {props.data.map((d,index)=>{
            return <div className={styles.individualCard}>
              <Card key={index}>
            <CardActionArea onClick={()=>handleClick(index)}>
              <CardMedia           
                image={d.imageDetails.url}
                title={d.title}
                className={styles.imgSize}
              />
              <CardContent>
                <Typography gutterBottom variant="body1" component="h1">
                 {d.title}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                  {d.channel}
                </Typography>               
              </CardContent>
            </CardActionArea>
            </Card>          
            </div>
        })}        
        </div>)    
        // <div className={styles.videoList}>
        //     <Divider section/>
        //     <Item.Group link>
        //     {
        //         props.data.map((d,index)=>{
        //             return <Item key={index} onClick={e=> handleClick(index)}>
        //             <Item.Image size='small' src={d.imageDetails.url} />
        //             <Item.Content>
        //           <Item.Header>{d.title}</Item.Header>
        //           <Item.Meta>{d.channel}</Item.Meta>
        //               <Item.Description>{d.description.substring(0,100)}</Item.Description>
        //             </Item.Content>
        //           </Item>
        //         })
        //     }
        //     </Item.Group>            
        // </div>
    //)
}

export default VideoList
