import React, {Component} from 'react';
import styles from './App.module.css';
import {getYoutubeData,fetchMostPopularVideos,getSimilarVideos} from './API/youtube';
import SelectedVideo from './Components/selectedVideo';
import VideoList from './Components/videoList';
import SearchComponent from './Components/search';
import {Typography} from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import YouTubeIcon from '@material-ui/icons/YouTube';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
class App extends Component{
  videoListData=[];
  selectedVideo={};
  similarVideos=[];
  constructor(props) {
    super(props)  
    this.state = {
       videoListData:[],
       selectedVideo:{},
       similarVideos:[],
       showIcon:false
    }
  } 
  async componentDidMount(){
    const data=await fetchMostPopularVideos();
    this.fetchVideoListData(data,'trending');
    this.setState({
      videoListData:this.videoListData
    })
  }
  handleSearch=async(searchedContent)=>{
    console.log(searchedContent);
    this.selectedVideo={};
    const result=await getYoutubeData(searchedContent);  
    console.log(result);  
    this.fetchVideoListData(result,'searched');
    console.log(this.videoListData);
    this.setState({
      videoListData:this.videoListData,
      selectedVideo:this.selectedVideo
    });
}
fetchVideoListData=(data,category)=>{
  this.videoListData=[];
  category === 'searched'? 
  this.videoListData=data.items.map((item)=>{
   return{
     videoId:item.id.videoId,
     channel:item.snippet.channelTitle,
     description:item.snippet.description,
     title:item.snippet.title,
     imageDetails:item.snippet.thumbnails.high
   }
 }):this.videoListData=data.items.map((item)=>{
  return{
    videoId:item.id,
    channel:item.snippet.channelTitle,
    description:item.snippet.description,
    title:item.snippet.title,
    imageDetails:item.snippet.thumbnails.high
  }
})

}
fetchSimilarVideoData=(similarVideoData)=>{
  this.similarVideos=[];
this.similarVideos=similarVideoData.items.map((item)=>{
  return{
    videoId:item.id.videoId,
    channel:item.snippet.channelTitle,
    description:item.snippet.description,
    title:item.snippet.title,
    imageDetails:item.snippet.thumbnails.high
  }
})
}
handleGoBack=()=>{
  this.setState({selectedVideo:{},showIcon:false})
}
handleVideoSelect=async(videoIndex,from)=>{
  this.selectedVideo={};
  if(from === 'search')
  {
    this.selectedVideo=this.videoListData.filter((videoData,index)=>index === videoIndex);
    console.log(this.selectedVideo);
  }
  else{
    this.selectedVideo=this.similarVideos.filter((videoData,index)=>index === videoIndex);
  }
const data=await getSimilarVideos(this.selectedVideo[0].videoId);
this.fetchSimilarVideoData(data);
this.setState({
  selectedVideo:this.selectedVideo[0],
  similarVideos:this.similarVideos,
  showIcon:true
});
}
  render(){
    console.log(this.state.selectedVideo);
    return <div> 
      {/* <div>
      {this.state.showIcon ? <IconButton onClick={this.handleGoBack}>
        <YouTubeIcon className={styles.youTubeIcon} color="secondary"/>
      </IconButton>:null}
           
        </div>                                 */}
         <SearchComponent onSearch={this.handleSearch}/>
      <br/>
      {
        Object.keys(this.state.selectedVideo).length ?  
        <SelectedVideo data={this.state.selectedVideo} 
        similarVideos={this.state.similarVideos}
        onSelectItem={this.handleVideoSelect}
        from="similar"/> : null
      }   
      {
        (this.state.videoListData.length && !Object.keys(this.state.selectedVideo).length)
        ? <VideoList onSelectItem={this.handleVideoSelect} data={this.state.videoListData}
        from="search"/> : null
      }        
    </div>
  }
}
export default App;
