import React ,{useRef,useEffect} from 'react';
import styles from './youtube.module.css';
import VideoList from './videoList';
const SelectedVideo=(props)=>{
    const video=useRef(null);
    useEffect(() => {
        console.log(video)
        video.current.focus();
    });
    console.log(props.data);
    console.log(props.similarVideos)
    const url=`https://www.youtube.com/embed/${props.data.videoId}?rel=0&hd=1&autoplay=1`;
    return (       
        <React.Fragment>
            <div ref={video} className={styles.videoPlayer}>         
        <iframe  src={url} title={props.data.title} frameBorder="0" allowFullScreen></iframe>                  
        </div>
        <br/>
        <br/>
        <br/>
        <VideoList data={props.similarVideos}
        onSelectItem={props.onSelectItem} from={props.from}/> 
        </React.Fragment>             
    )
}
export default SelectedVideo;
